
import {
  IonItem,
  IonContent,
  modalController,
  toastController,
} from "@ionic/vue";

import { defineComponent } from "vue";
export default defineComponent({
  name: "con",
  props: {
    // buyer: String,
    // grain: String,
    // month: String,
    // price: String,
    Station: String,
    Date: String,
    Amount: String,
    Moisture: String,
    Crop: String,
    Editable: Boolean,
  },
  components: {
    IonItem,
    IonContent,
  },
  data() {
    return {
      content: "Content",
    };
  },
  methods: {
    async fetchMessages() {
      console.log("Get data");
      // return []
      const mydata = {
        month: "052021",
        day: 28,
        // @ts-ignore
        buyer: document.getElementById("station").value,
        // @ts-ignore
        amount: document.getElementById("amount").value,
        // @ts-ignore
        moisture: document.getElementById("moisture").value,
        // @ts-ignore
        crop: "Soybean",
        periodNumber: 1,
      };
      const response = await fetch("https://farmer.weathon.top/api/newDelivery", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        }, 
        body: JSON.stringify(mydata),
      });
      return response.ok;
    },
    async openToast() {
      const toast = await toastController.create({
        message: "Successfully confirmed.",
        duration: 2000,
        color: "dark",
        // translucent: true
      });
      return toast.present();
    },
    c() {
      this.fetchMessages()
      modalController.dismiss();
      this.openToast();
    },
  },
});
